<template>
  <div
    class="container py-10 mx-auto"
    v-if="allChapters && allChapters.length > 0"
  >
    <div>
      <div class="space-y-3">
        <div
          v-for="(element, i) in allChapters"
          :key="i"
          class="flex items-center py-3 bg-gray-200 rounded"
        >
          <div class="text-white md:mx-5">
            <button
              @click="allChapters[i].selected = !allChapters[i].selected"
              class="flex items-center justify-center w-12 h-12 m-2 transition duration-200 bg-green-400 rounded-full shadow-md  animate__animated animate__pulse"
            >
              <span class="material-icons"
                >{{
                  allChapters[i].selected ? "done" : "check_box_outline_blank"
                }}
              </span>
            </button>
          </div>
          <div>
            <h2 class="text-xl font-semibold bangla">
              {{ element.chapter }}
            </h2>
            <h2 class="text-lg font-semibold">{{ element.subject }}</h2>
          </div>
        </div>
      </div>
      <div class="mt-5 text-center">
        <button
          @click="selectedChaptersToParent"
          class="px-6 py-3 font-semibold text-white transition duration-200 shadow  hover:shadow-lg bg-gradient-to-tl from-indigo-500 hover:from-blue-700 to-indigo-800"
        >
          Next
        </button>
      </div>
    </div>
  </div>
  <div v-else class="h-screen text-center">
    <p class="mt-10 text-center">Please wait...</p>
  </div>
</template>

<script>
import { reactive } from "@vue/reactivity";
import { routine } from "../plugins/routine";
export default {
  $emits: ["selectedChapters"],
  setup(props, { emit }) {
    let allChapters = reactive(routine.routines.filter((c) => c.ss22));

    const selectedChaptersToParent = () => {
      let Chapters = allChapters;
      let chapters = Chapters.filter((chapter) => chapter.selected);

      emit("selectedChapters", chapters);
    };

    return { allChapters, selectedChaptersToParent };
  },
};
</script>

<style lang="scss" scoped>
</style>