<template>
  <div class="grid min-h-screen place-items-center">
    <template v-if="step == 0">
      <RoutineOptions @onNext="onNext" />
    </template>
    <!-- <template v-if="step == 1">
      <SelectGroup @selectedGroup="setGroup" />
    </template> -->
    <template v-if="step == 1">
      <SelectChapters @selectedChapters="setSelectedChapters" />
    </template>
    <template v-if="step == 2">
      <OrganizeChapters
        @sortedChapters="setSortedChapters"
        :selectedChaptersToSort="selectedChaptersToSort"
      />
    </template>
    <template v-if="step == 3">
      <CompleteRoutine
        :routine="routine"
        :completedRoutine="completedRoutine"
      />
    </template>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { useRoute, useRouter } from "vue-router";
import RoutineOptions from "../components/RoutineOptions.vue";
import SelectChapters from "../components/SelectChapters.vue";
// import SelectGroup from "../components/SelectGroup.vue";
import OrganizeChapters from "../components/OrganizeChapters.vue";
import { useStore } from "vuex";
import _ from "lodash";
import CompleteRoutine from "../components/CompleteRoutine.vue";
export default {
  components: {
    RoutineOptions,
    SelectChapters,
    // SelectGroup,
    OrganizeChapters,
    CompleteRoutine,
  },
  setup() {
    const router = useRouter();
    const route = useRoute();
    const store = useStore();

    let step = ref(route.query.step || 0);

    const onNext = () => {
      step.value++;
      router.push({
        query: {
          step: step.value,
        },
      });
    };

    const onPrev = () => {
      step.value--;
      router.push({
        query: {
          step: step.value,
        },
      });
    };

    let group = ref("");
    let routine = ref([]);
    let completedRoutine = ref("");
    const setGroup = (selectedGroup) => {
      group.value = selectedGroup;

      onNext();
    };

    let selectedChaptersToSort = ref([]);

    const setSelectedChapters = (chapters) => {
      selectedChaptersToSort.value = chapters;
      onNext();
    };

    const setSortedChapters = (chapters) => {
      let targetChapters = chapters;
      let { start, days } = store.state.options;
      let totalDays = parseInt(366);
      let targetDays = parseInt(days);

      let totalper = _.sumBy(targetChapters, (k) => (k.dtoc / totalDays) * 100);
      let startDate = new Date(start).getTime();
      routine.value = targetChapters.map((r) => {
        let percentage = (r.dtoc / totalDays) * 100;
        let factor = targetDays / totalper;
        let cdays = r.selected ? percentage * factor : 0;
        let endDate = new Date(startDate + 24 * cdays * 3600000);
        let chapter = {
          id: r.id,
          subject: r.subject,
          chapter: r.chapter,
          days: 24 * cdays * 3600000,
          start: new Date(startDate).toLocaleString(),
          end: endDate.toLocaleString(),
          roundDays: Math.ceil(cdays),
        };
        startDate = endDate.getTime();
        return chapter;
      });

      let rtin = routine.value.map((r) => ({
        id: r.id,
        start: new Date(r.start).getTime(),
        end: new Date(r.end).getTime(),
        complete: false,
      }));

      completedRoutine.value = JSON.stringify({
        row: store.state.user.row,
        setRoutine: true,
        routine: rtin,
      });

      onNext();
    };

    return {
      step,
      onNext,
      onPrev,
      setGroup,
      setSelectedChapters,
      setSortedChapters,
      selectedChaptersToSort,
      routine,
      completedRoutine,
    };
  },
};
</script>

