<template>
  <div class="container relative p-3 shadow-md rounded-xl bg-secondary md:p-5">
    <div class="flex items-center justify-center">
      <img
        src="https://img.icons8.com/external-inipagistudio-mixed-inipagistudio/64/000000/external-routine-mental-wellbeing-inipagistudio-mixed-inipagistudio.png"
      />
    </div>
    <h2 class="my-3 text-2xl font-semibold text-center">Select Your Plan</h2>

    <div>
      <form @submit.prevent="submit">
        <!-- Name -->
        <div class="mb-3">
          <label class="inline-block mb-2 dark:text-white">Starting Date</label>
          <v-date-picker v-model="start" :update-on-input="false">
            <template v-slot="{ inputValue, inputEvents }">
              <input
                required
                class="
                  block
                  w-full
                  py-1.5
                  px-3
                  text-gray-800
                  border border-gray-300
                  transition
                  duration-500
                  focus:outline-none
                  focus:border-black
                  rounded
                "
                name="start"
                placeholder="When do you want to start?"
                :value="inputValue"
                v-on="inputEvents"
              />
            </template>
          </v-date-picker>

          <ErrorMessage class="text-red-600 dark:text-red-500" name="start" />
        </div>
        <div class="mb-3">
          <label class="inline-block mb-2 dark:text-white">Ending Date</label>
          <v-date-picker v-model="end" :update-on-input="false">
            <template v-slot="{ inputValue, inputEvents }">
              <input
                required
                class="
                  block
                  w-full
                  py-1.5
                  px-3
                  text-gray-800
                  border border-gray-300
                  transition
                  duration-500
                  focus:outline-none
                  focus:border-black
                  rounded
                "
                name="end"
                placeholder="When do you want to complete?"
                :value="inputValue"
                v-on="inputEvents"
              />
            </template>
          </v-date-picker>
          <ErrorMessage class="text-red-600 dark:text-red-500" name="end" />
        </div>
        <div class="mb-3">
          <label class="inline-block mb-2 dark:text-white">Total Days</label>
          <input
            type="text"
            v-model="days"
            readonly
            class="
              block
              w-full
              py-1.5
              px-3
              text-gray-800
              border border-gray-300
              transition
              duration-500
              focus:outline-none
              focus:border-black
              rounded
            "
          />
        </div>
        <div class="text-center">
          <button
            class="inline-flex items-center px-6 py-3 font-semibold text-white transition duration-200 shadow  hover:shadow-lg bg-primary"
          >
            Next <span class="ml-2 material-icons">arrow_forward</span>
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { computed, ref } from "@vue/reactivity";
import { useStore } from "vuex";
import { DatePicker } from "v-calendar";
export default {
  name: "RoutineOptions",
  components: {
    "v-date-picker": DatePicker,
  },
  setup(_, { emit }) {
    const schema = {
      start: "required",
      end: "required",
    };

    const store = useStore();

    let start = ref(null);
    let end = ref(null);

    const submit = () => {
      if (days.value > 0) {
        store.dispatch("setOptions", {
          options: { days: days.value, start: start.value },
        });

        emit("onNext");
      }
    };
    const days = computed(() => {
      let d = parseInt(
        (new Date(end.value).getTime() - new Date(start.value).getTime()) /
          (1000 * 3600 * 24)
      );

      return d > 0 ? d : 0;
    });

    return {
      start,
      end,
      schema,
      submit,
      days,
    };
  },
};
</script>

<style lang="scss" scoped>
</style>