<template>
  <div
    class="container px-2 py-10 mx-auto"
    v-if="allChapters && allChapters.length > 0"
  >
    <draggable
      handle=".handle"
      v-model="allChapters"
      tag="transition-group"
      :component-data="{ tag: 'div', name: 'flip-list', type: 'transition' }"
      item-key="id"
      class="space-y-5"
    >
      <template #item="{ element }">
        <div class="flex items-center py-3 bg-gray-200 rounded">
          <button class="inline-block px-3 cursor-move handle">
            <span class="material-icons"> drag_indicator </span>
          </button>
          <div class="flex items-center justify-center w-full">
            <div class="text-center">
              <h2 class="text-xl font-semibold bangla">
                {{ element.chapter }}
              </h2>
              <h2 class="text-lg font-semibold">{{ element.subject }}</h2>
            </div>
          </div>
        </div>
      </template>
    </draggable>
    <div class="mt-5 text-center">
      <button
        @click="sortedChaptersToParent"
        class="px-6 py-3 font-semibold text-white transition duration-200 shadow  hover:shadow-lg bg-gradient-to-tl from-indigo-500 hover:from-blue-700 to-indigo-800"
      >
        Next
      </button>
    </div>
  </div>

  <div v-else class="h-screen text-center">
    <p class="mt-10 text-center">Please wait...</p>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import draggable from "vuedraggable";
// import { useStore } from "vuex";
export default {
  components: { draggable },
  $emits: ["sortedChapters"],
  props: {
    selectedChaptersToSort: {
      type: Array,
      required: true,
    },
  },

  setup(props, { emit }) {
    let selectedChapters = props.selectedChaptersToSort.map((c) => c);
    let allChapters = ref(selectedChapters);

    const sortedChaptersToParent = () => {
      emit("sortedChapters", allChapters.value);
    };

    return { allChapters, sortedChaptersToParent };
  },
};
</script>

<style lang="scss" scoped>
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
</style>