<template>
  <div class="pb-10">
    <div class="my-5">
      <h2 class="text-2xl font-semibold text-center">Your Prepared Routine</h2>
    </div>
    <div class="space-y-5">
      <div v-for="(r, i) in routine" :key="i">
        <div class="p-5 text-center bg-gray-200">
          <h2 class="text-2xl font-semibold bangla">{{ r.chapter }}</h2>
          <h2 class="text-xl font-semibold">{{ r.subject }}</h2>
          <h3 class="text-lg font-medium">
            Duration: {{ getDuration(r.days) }}
          </h3>
          <div class="flex justify-center gap-3 my-3">
            <p
              class="p-2 font-medium bg-gray-300 border border-gray-400 border-dashed "
            >
              Starts: {{ formattedDate(r.start) }}
            </p>
            <p
              class="p-2 font-medium bg-gray-300 border border-gray-400 border-dashed "
            >
              Ends: {{ formattedDate(r.end) }}
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="my-4 text-center">
      <button
        :disabled="loading"
        @click="save"
        class="px-6 py-2 font-semibold text-white bg-green-500 rounded shadow"
      >
        {{ loading ? "Please wait..." : "Submit" }}
      </button>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { useDateFormat } from "../hooks/dateFormat";
import { auth } from "../plugins/auth";
import { useRouter } from "vue-router";
export default {
  props: {
    routine: {
      type: Array,
    },
    completedRoutine: {
      required: true,
    },
  },
  setup(props) {
    let loading = ref(false);
    const router = useRouter();
    const { formattedDate, getDuration } = useDateFormat();
    const save = async () => {
      loading.value = true;
      const res = await fetch(auth, {
        method: "POST",
        body: props.completedRoutine,
      });
      const data = await res.json();
      if (data.message == "success!") {
        loading.value = false;
        localStorage.setItem(
          "routines22",
          JSON.stringify(JSON.parse(props.completedRoutine).routine)
        );
        router.push("/");
      } else {
        alert("An error occurred!");
      }
    };

    return { loading, formattedDate, getDuration, save };
  },
};
</script>

